* {
  margin: 0;
  padding: 0;
}

.Home {
    font-family: Arial, sans-serif;
    text-align: center;
  }

  .header {
    background-color: #f4f4f4;
    padding: 20px;
    border-bottom: 2px solid #ddd;
    width: 100%;
    height: 50vh; 
    box-sizing: border-box; 
    display: flex; 
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form-container {
    margin-top: 20px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input {
    padding: 8px;
    width: 100%;
    max-width: 300px;
    box-sizing: border-box;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }

  /*Responsividade*/

  @media (max-width: 600px) {
    .header h1 {
      font-size: 2em;
    }
  
    .header p {
      font-size: 1em; 
    }
  
    .form-container {
      padding: 20px;
      width: 90%;
    }
  }
 
  @media (min-width: 300px) and (max-width: 599px) {
    .form-container {
      padding: 5px !important;
      width: 70% !important;
    }
  }